.container {
  display: flex;
}
aside {
  flex: 3;
}
.postContainer {
  flex: 9;
}
.comments-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.commentForm {
  width: 90%;
  display: flex;
  flex-direction: column;
  margin: 10px 0;
  font-family: "Open Sans", sans-serif;
}
.commentForm h3 {
  margin: 10px 0;
  font-weight: 400;
}
.commentForm textarea {
  height: 100px;
  font-size: 14px;
  border-radius: 5px;
  resize: none;
}
.commentForm textarea:focus {
  border-color: teal;
}
.commentForm button {
  width: 150px;
  margin: 5px 0;
  background-color: teal;
  color: white;
  padding: 10px 0;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
.commentForm button:disabled {
  background-color: gray;
  cursor: not-allowed;
}
