.writeSec {
  margin: 20px auto;
  width: 60vw;
}
.writeImg {
  width: 100%;
  height: 250px;
  border-radius: 7px;
  object-fit: cover;
}
.form-controle {
  display: flex;
  margin: 10px 0;
  align-items: center;
}
.writeSec .form-controle input,
.form-controle textarea {
  flex: 1;
  font-family: "Open Sans", sans-serif;
  border: none;
  padding: 10px 20px;
  outline: none;
}
.writeSec .form-controle input {
  font-size: 20px;
}
.writeSec .form-controle textarea {
  font-size: 16px;
  height: 400px;
  resize: none;
}
textarea::-webkit-scrollbar-track {
  background-color: #f5f5f5;
  border-radius: 5px;
}
.writeSec .form-controle select {
  flex: 1;
  font-family: "Open Sans", sans-serif;
  outline: none;
  font-size: 18px;
  text-transform: capitalize;
  padding: 8px 20px;
}
.writeSec .form-controle select option {
  background-color: rgba(0, 0, 0, 0.6);
  color: white;
  outline: none;
}
textarea::-webkit-scrollbar {
  width: 10px;
  background-color: #f5f5f5;
  border-radius: 5px;
}

textarea::-webkit-scrollbar-thumb {
  background-color: teal;
  border-radius: 5px;
  cursor: default;
}
.form-controle i {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  border: 1px solid black;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  cursor: pointer;
}
form {
  position: relative;
}
.publishPostBtn {
  position: absolute;
  top: 10px;
  right: -100px;
  padding: 8px 18px;
  background: teal;
  color: white;
  border: none;
  font-weight: 600;
  font-size: 16px;
  border-radius: 5px;
  font-family: "Open Sans", sans-serif;
  cursor: pointer;
}
button:disabled {
  background-color: gray;
  cursor: not-allowed;
}
